import http from '../httpConfig'
const table = 'mrbase_sys_menus'
// 菜单管理列表
export const get_menuList = (params = {}) => {
    params['~table~'] = table
    return new Promise((resolve, reject) => {
        http.post('/api/menu/list-menu/v1', params)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}
