<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div>
      <mrc_ess_iconGroup :is_disabled="disabled"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-form ref='menuDetailRef'>
        <v-card>
          <v-card-text>
            <v-row dense>
              <v-col cols="12"
                     sm="2"
                     class="d-flex">
                <v-switch :label="detailData.is_show?$t('label_display'): $t('label_hidden')"
                          color="primary"
                          v-model="detailData.is_show"
                          :readonly="disabled"
                          :true-value="1"
                          :false-value="0"></v-switch>
              </v-col>
              <v-col cols="12"
                     sm="5"
                     offset-sm="5"
                     class="d-flex"
                     style="justify-content: flex-end">
                <span v-if="uuid">id: {{uuid}}</span>

              </v-col>
              <!-- <v-col cols="12"
                     sm="12"
                     class="d-flex">
                <v-text-field :readonly="disabled"
                              :label="$t('label_Menu_name')+'*'"
                              v-model="detailData.menu_name"
                              append-icon="mdi-longitude"
                              :rules="$text_rules('菜单名称')">
                </v-text-field>
              </v-col>
              <v-col cols="12"
                     sm="12"
                     class="d-flex">
                <v-text-field :label="$t('label_describe')+'*'"
                              :readonly="disabled"
                              v-model="detailData.menu_description"
                              append-icon="mdi-longitude"
                              :rules="$text_rules('描述')">
                </v-text-field>
              </v-col> -->
              <v-col cols="12"
                     sm="12"
                     class="d-flex">
                <mrc_ess_multiText :readonly="disabled"
                                   :label="$t('label_Menu_name')+'*'"
                                   :isNational="judegNational('menu_name')"
                                   v-model="detailData.menu_name"
                                   style="width:100%"
                                   :rules="$text_rules('菜单名称')">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="12"
                     sm="12"
                     class="d-flex">
                <mrc_ess_multiText :label="$t('label_describe')+'*'"
                                   :isNational="judegNational('menu_description')"
                                   :readonly="disabled"
                                   style="width:100%"
                                   v-model="detailData.menu_description"
                                   :rules="$text_rules('描述')">
                </mrc_ess_multiText>
              </v-col>
              <v-col cols="2"
                     class="d-flex">
                <v-checkbox color="primary"
                            :readonly="disabled"
                            v-model="detailData.is_catalog"
                            :true-value="0"
                            :false-value="1"
                            :label="$t('label_Directories_do_not_contain_functionality')+'*'"></v-checkbox>
              </v-col>
              <v-col cols="10"
                     class="d-flex">
                <mrc_ess_prompt :label="$t('label_Corresponding_function')+'*'"
                                v-show="detailData.is_catalog"
                                :rules='function_uuid_rules'
                                :disabled='disabled'
                                v-model="detailData.function_uuid"
                                prompt_name='function_module_parent'>
                </mrc_ess_prompt>
                <v-select readonly
                          :label="$t('label_Corresponding_function')+'*'"
                          v-show="!detailData.is_catalog"></v-select>
              </v-col>
              <v-col cols="12"
                     class="d-flex">
                <mrc_ess_prompt :label="$t('label_Menu_superior_name')+'*'"
                                :rules='false'
                                :chips='false'
                                :disabled='disabled'
                                v-model="detailData.tricode_parent"
                                :select_arr='menu_parent_list'>
                </mrc_ess_prompt>
              </v-col>

              <v-col cols="12"
                     sm="6"
                     class="d-flex">
                <v-text-field :readonly="disabled"
                              :label="$t('label_upload_image')+'*'"
                              v-model="detailData.menu_icon"
                              :rules="$text_rules('上传图标')">
                </v-text-field>
              </v-col>
              <v-col cols="12"
                     sm="1"
                     class="d-flex"
                     style="justify-content: flex-end">
                <div :style="{width: '20px',height: '20px',backgroundColor: detailData.menu_color}">

                </div>
              </v-col>
              <v-col cols="12"
                     sm="5"
                     class="d-flex">
                <mrc_ess_colorPicker :disabled='disabled'
                                     v-model="detailData.menu_color">
                  <template v-slot:showAction>
                    <v-text-field :label="$t('label_color')+'*'"
                                  readonly
                                  v-model="detailData.menu_color"
                                  :rules="[v=> v != '' || '颜色']">
                    </v-text-field>
                  </template>
                </mrc_ess_colorPicker>
              </v-col>
              <v-col cols="12"
                     sm="6"
                     class="d-flex">
                <v-text-field :readonly="disabled"
                              :label="$t('label_The_sorting')+'*'"
                              v-model="detailData.sequence"
                              :rules="$text_rules('排序')">
                </v-text-field>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-form>
      <mrc_ess_submitBtn v-show="!disabled"
                         :loading='loading'
                         @cancel='cancel'
                         @success='success'>
      </mrc_ess_submitBtn>
    </div>
  </mrc_ess_skLoaders>
</template>

<script>
import { getBusinessList } from '@/service/api/prompt'
import { get_menuDetail, add_menu, update_menu, del_menu } from '@/service/api/menu'
export default {
  components: {},
  props: {},
  data () {
    return {
      skLoading: true,
      loading: false,
      uuid: '',
      disabled: false,
      detailData: {
        menu_name: '',
        menu_description: '',
        sequence: 1, // 排序
        menu_icon: '',
        menu_color: '',
        is_show: 1,
        function_uuid: null,
        is_catalog: 1,
        tricode_parent: ''
      },
      menu_parent_list: []
    };
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_menus')
    this.skLoading = true
    const name = this.$store.getters.subPage_name || '新菜单'
    const uuid = this.$route.params.uuid || this.$store.state.uuid
    this.$route.meta.title = name
    if (uuid) {
      this.uuid = uuid
      const detail = await get_menuDetail({ uuid })
      if (detail.code == 200) {
        this.detailData = detail.data
      }
      this.disabled = true
    } else {
      this.disabled = false
    }
    const menu_parent_list = await getBusinessList({ prompt_name: 'menu_parent', uuid: this.uuid })
    if (menu_parent_list.code == 200) {
      let data = menu_parent_list.data
      data.unshift({ view_fields: '无', value_field: null })
      const index = data.findIndex(item => item.value_field == this.detailData.tricode)
      if (index != -1 && data[0].view_fields !== '无') {
        data.splice(index, 1)
      }
      this.menu_parent_list = data
    }
    this.skLoading = false
  },
  methods: {
    // 返回上一页
    handleReturn () {
      this.$router.go(-1)
    },
    // 修改按钮
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({
        title: '提示',
        content: '确定删除吗？'
      }).then(async () => {
        const del = await del_menu({ uuid: this.detailData.uuid })
        if (del.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
          this.$router.go(-1)
        }
      }).catch(() => {
        console.log('点了取消')
      })

    },
    // 取消
    cancel () {
      this.$myConfirm({
        title: '提示',
        content: '确认放弃修改'
      }).then(() => {
        this.$router.go(-1)
      }).catch(() => {
        console.log('点了取消')
      })
    },
    // 提交
    async success () {
      const isSubmit = this.$refs.menuDetailRef.validate()
      if (!isSubmit) return

      const obj = this.$clone(this.detailData)
      if (!obj.is_catalog) {
        obj.function_uuid = null
        this.detailData.function_uuid = null
      }
      if (!obj.tricode_parent) {
        obj.tricode_parent = null
      }
      // console.log(obj)
      // return
      // 修改
      this.loading = true
      if (this.uuid) {
        delete obj.created_utc_datetime
        delete obj.updated_utc_datetime
        const update = await update_menu(obj)
        if (update.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          this.$router.go(-1)
        }
        this.loading = false
      } else {
        // 新增
        const add = await add_menu(obj)
        if (add.code == 200) {
          this.$store.commit('newsTips', this.$t('message_added_successfully'))
          this.$router.go(-1)
        }
        this.loading = false
      }
    },
  },
  computed: {
    function_uuid_rules () {
      return this.detailData.is_catalog ? this.$t('verify_Required') : false
    }
  },
  watch: {
  },
};
</script>

<style scoped>
.d-flex {
  align-items: center;
}
</style>
